<template>
  <div>
    <Page>
      <b-form>
        <b-form-row>
          <b-form-group label="Data Início" class="col-md-auto">
            <date-picker
              v-model="time1"
              format="DD-MM-YYYY HH:mm:ss"
              class="mt-1"
              type="datetime"
            ></date-picker>
          </b-form-group>
          <b-form-group label="Data Final" class="col-md-auto">
            <date-picker
              v-model="time2"
              format="DD-MM-YYYY HH:mm:ss"
              class="mt-1"
              type="datetime"
            ></date-picker>
          </b-form-group>
          <b-form-group label="Campanhas" class="col-md-auto">
            <b-select plain v-model="queue" :options="queues" size="md">
              <b-select-option value="">Todas as Campanhas</b-select-option>
            </b-select>
          </b-form-group>
          <b-form-group label="Agentes" class="col-md-auto">
            <b-select value="Select state" :options="agents" v-model="agent">
              <b-select-option value="">Todos os Agentes</b-select-option>
            </b-select>
          </b-form-group>
        </b-form-row>
        <b-form-row>
          <ladda-btn
            :loading="loading"
            data-style="expand-left"
            class="btn btn-success"
            @click.native="getMenuStatusDetail"
            >Visualizar</ladda-btn
          >
        </b-form-row>
      </b-form>
    </Page>
    <Page :hidden="table.length == 0">
      <Table :tableData="table" :filename="fileName" :header="header" :legend="legend" />
    </Page>

    <Page :hidden="tableAgent2.length == 0">
      <Table
        :tableData="tableAgent"
        :filename="fileName"
        :header="header"
        :legend="legend"
      />
    </Page>
  </div>
</template>
<script>
import Page from "@/components/Page";
import DatePicker from "vue2-datepicker";
import "@/vendor/libs/vue2-datepicker/locale/pt-br";
import moment from "moment";
import LaddaBtn from "@/vendor/libs/ladda/Ladda";
import Table from "@/components/Table";
import axios from "axios";
export default {
  name: "menu-status",
  components: {
    Page,
    DatePicker,
    LaddaBtn,
    Table,
  },
  metaInfo: {
    title: "Relatório de Menu Status",
  },
  data: () => ({
    loading: false,
    phoneMask0: [
      "(",
      /[1-9]/,
      /\d/,
      ")",
      " ",
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
    ],
    phoneMask1: [
      "(",
      /[1-9]/,
      /\d/,
      ")",
      " ",
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
    ],
    phoneMask2: [
      "(",
      /[1-9]/,
      /\d/,
      ")",
      " ",
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
    ],
    phone: "",
    time1: moment().hour(0).minute(0).second(0).toDate(),
    time2: moment().hour(23).minute(59).second(59).toDate(),
    noanswered: false,
    busy: false,
    tel: "",
    table: [],
    tableAgent: [],
    newTable1: [],
    tableAgent2: [],
    header: [],
    legend: [],
    fileName: "",
    tempTaple: [],
    queues: [],
    queue: "",
    agents: [],
    agent: "",
    agentName: "",
    queueWaitTime: "",
    agentHoldTime: "",
    reason: "",
    callTime: "",
    numInQueue: "",
    badAgents: "",
    agentAnswerTime: "",
    callReceived: "",
    queueName: "",
    callerNum: "",
  }),
  mounted() {
    this.$store.commit("setTitle", "Relatório de Menu Status");
    this.loadQueues();
    this.loadAgents();
  },
  methods: {
    loadQueues: function () {
      axios
        .post(`${location.origin}/reportsbackend/queues.php`)
        .then((data) => {
          this.queues = data.data;
        })
        .catch((err) => console.log(err));
    },
    loadAgents: function () {
      axios
        .post(`${location.origin}/reportsbackend/agents.php`)
        .then((data) => {
          this.agents = [];

          data.data.forEach((x) => {
            this.agents.push(x.agent);
          });
        })
        .catch((err) => console.log(err));
    },
    showNoRecordToast() {
      this.$bvToast.toast("Nenhum registro encontrado", {
        title: "Tecnomafer Report",
        autoHideDelay: 5000,
        appendToast: true,
        toastClass: "bs4-toast bg-danger",
      });
    },
    async getMenuStatusDetail() {
      try {
        this.loading = true;
        const params = new URLSearchParams();
        params.append("time1", moment(this.time1).format("YYYY-MM-DD HH:mm:ss"));
        params.append("time2", moment(this.time2).format("YYYY-MM-DD HH:mm:ss"));
        params.append("queue", this.queue);
        params.append("agent", this.agent);

        const response1 = await axios.post(
          `${location.origin}/reportsbackend/report_menu_status_detailed.php`,
          params
        );
        if (response1.data.length === 0) {
          this.showNoRecordToast();
        }
        const newTable = this.createNewTable(response1.data);
        this.updateHeader();
        this.table = newTable;
        const response2 = await axios.post(
          `${location.origin}/reportsbackend/report_menu_status_agent.php`,
          params
        );
        if (response2.data.length === 0) {
          this.showNoRecordToast();
        }
        this.tableAgent = this.createNewTableAgent(response2.data);
        this.updateHeader();
      } catch (error) {
        console.log(error);
      } finally {
        this.loading = false;
      }
    },
    createNewTable(data) {
      data.forEach((row, index) => {
        if (row.event === "CONNECT") {
          const nextIndex = data.findIndex(
            (nextRow, nextIndex) =>
              nextIndex > index &&
              ((row.Operador === nextRow.Operador && nextRow.event === "COMPLETEAGENT") ||
                (row.Operador === nextRow.Operador && nextRow.event === "COMPLETECALLER"))
          );

          if (nextIndex !== -1) {
            data[nextIndex]["Tempo de espera do operador"] =
              row["Tempo de espera no operador (s)"];
            data[nextIndex]["Recebido pelo operador"] = row["Recebido pelo operador"];
          }
        }
      });
      data = data.filter((row) => row.event !== "CONNECT");

      data.forEach((row, index) => {
        if (row.event === "COMPLETEAGENT" || row.event === "COMPLETECALLER") {
          const horaRecebido = moment(row["Recebido pelo operador"], "HH:mm:ss");
          const duracaoSegundos = moment.duration(row.Conversacao).asSeconds();

          const novaHora = horaRecebido.add(duracaoSegundos, "seconds");

          const sum =
            Number(row["Tempo de espera do operador"]) +
            Number(row["Tempo Espera na fila (s)"]);

          this.newTable1.push({
            Data: row.Data,
            Fila: row.fila,
            Origem: row.Origem,
            Operador: row.Operador,
            Recebido: row.Recebido,
            "Tempo de espera": this.segundosParaFormatoHora(sum),
            "Recebido pelo operador": row["Recebido pelo operador"],
            "Tempo de conversação": row.Conversacao,
            "Chamada encerrada": novaHora.format("HH:mm:ss"),
            "Motivo da Conclusão": row["Motivo da conclusão"],
            Intervalo: row.Intervalo,
          });
        } else if (row.event === "ABANDON") {
          const sum =
            Number(row["Tempo de espera no operador (s)"]) +
            Number(row["Tempo Espera na fila (s)"]);
          this.newTable1.push({
            Data: row.Data,
            Fila: row.fila,
            Origem: row.Origem,
            Operador: "ABANDON",
            Recebido: row.Recebido,
            "Tempo de espera": this.segundosParaFormatoHora(sum),
            "Recebido pelo operador": row["Recebido pelo operador"],
            "Tempo de conversação": row.Conversacao,
            "Chamada encerrada": row["Chamada encerrada"],
            "Motivo da Conclusão": row["Motivo da conclusão"],
            Intervalo: row.Intervalo,
          });
        }
      });

      return this.newTable1;
    },
    createNewTableAgent(data) {
      const totalChamadas = data.reduce(
        (accumulator, row) => accumulator + parseInt(row.Chamadas),
        0
      );
      const totaisPorAgente = {};
      this.table.forEach((objeto) => {
        const nomeDoAgente = objeto.Operador;
        const tempoDeEspera = objeto["Tempo de espera do operador (s)"];

        if (nomeDoAgente !== "ABANDON" && totaisPorAgente[nomeDoAgente]) {
          totaisPorAgente[nomeDoAgente] += parseInt(tempoDeEspera);
        } else if (nomeDoAgente !== "ABANDON") {
          totaisPorAgente[nomeDoAgente] = parseInt(tempoDeEspera);
        }
      });
      data.forEach((row, index) => {
        const nomeDoAgente = row.Agente;
        const tempoDeEsperaOperador = this.segundosParaFormatoHora(
          Number(totaisPorAgente[nomeDoAgente])
        );
        const tempoDeEsperaTotal = this.somarHoras(
          row["Tempo Espera Na Fila"],
          tempoDeEsperaOperador
        );
        this.tableAgent2.push({
          Agente: row.Agente,
          Chamadas: row.Chamadas,
          "% Chamadas": ((row.Chamadas / totalChamadas) * 100).toFixed(2) + " %",
          "tempo de espera": tempoDeEsperaTotal,
          "Tempo De Conversação": row.Conversacao,
          "Tempo Médio de Conversação": row["Tempo Médio de Conversação"],
          "Completado pelo agente": row.Chamadas_COMPLETEAGENT,
          "Ligação completada": row.Chamadas_COMPLETECALLER,
        });
      });
      return this.tableAgent2;
    },
    segundosParaFormatoHora(segundos) {
      const duration = moment.duration(segundos, "seconds");
      const horas = Math.floor(duration.asHours());
      const minutos = moment.utc(duration.asMilliseconds()).format("mm");
      const segundosFormatados = moment.utc(duration.asMilliseconds()).format("ss");

      return `${horas}:${minutos}:${segundosFormatados}`;
    },
    somarHoras(tempo1, tempo2) {
      const formato = "HH:mm:ss";

      const duracao1 = moment.duration(tempo1, formato);
      const duracao2 = moment.duration(tempo2, formato);

      const resultado = duracao1.add(duracao2);

      return moment.utc(resultado.asMilliseconds()).format(formato);
    },
    updateHeader() {
      this.header = [];
      this.header.push("<h4>Relatório de Menu Status</h4>");
      this.header.push(
        `Período ${moment(this.time1).format("DD/MM/YYYY")} à ${moment(this.time2).format(
          "DD/MM/YYYY"
        )} / Hora à ${moment(this.time1).format("HH:mm:ss")} até ${moment(
          this.time2
        ).format("HH:mm:ss")}`
      );
      this.header.push(" ");

      this.legend = [
        "<h5>Legenda:</h5>",
        `<b>Emitido em:</b> ${moment().format("DD/MM/YYYY HH:mm:ss")}`,
      ];

      this.fileName = `Relatório de Menu Status ${moment().format(
        "DD_MM_YYYY_HH_mm_ss"
      )}`;
    },
  },
};
</script>
